import Component from '../react/component'
const ReactDom = {
    render
}
function render(vnode, container) {
    return container.appendChild(_render(vnode))
}
function createComponent(comp, props) {
    let inst
    if (comp.prototype && comp.prototype.render) {
        //Â¶ÇÊûúÊòØÁ±ªÁªÑ‰ª∂ ÂàôÂàõÂª∫ÂÆû‰æã
        inst = new comp(props)
    } else {
        //Â¶ÇÊûúÊòØÂáΩÊï∞ÁªÑ‰ª∂ Â∞ÜÂáΩÊï∞ÁªÑ‰ª∂Êâ©Â±ïÊàêÁ±ªÁªÑ‰ª∂
        inst = new Component(props)
        inst.constructor = comp
        inst.render = function () {
            return this.constructor(props)
        }
    }
    return inst
    //Â¶ÇÊûúÊòØÂáΩÊï∞ÁªÑ‰ª∂
}
function renderComponent(comp) {
    const renderer = comp.render()
    _render(renderer)
}
function setComponentProps(comp, attrs) {
    if (!comp.base) {
        if (comp.componentWillMount) comp.componentWillMount()
        else if (comp.componentWillReceiveProps) componentWillReceiveProps()
    }
    //ËÆæÁΩÆÁªÑ‰ª∂ÁöÑÂ±ûÊÄß
    comp.props = props
    //Ê∏≤ÊüìÁªÑ‰ª∂
    let base = renderComponent(comp)
    comp.base = base
}
function _render(vnode) {
    if (vnode === undefined || vnode === null || typeof vnode === 'boolean') {
        return
    }
    if (typeof vnode === 'string') {
        //ÂàõÂª∫ÊñáÊú¨ËäÇÁÇπ
        return document.createTextNode(vnode)
    }
    //Â¶ÇÊûútagÊòØÂáΩÊï∞
    if (typeof vnode.tag === 'function') {
        //1ÂàõÂª∫ÁªÑ‰ª∂
        const comp = createComponent(vnode.tag, vnode.attrs)
        //2 ËÆæÁΩÆÁªÑ‰ª∂ÁöÑÂ±ûÊÄß
        setComponentProps(comp, vnode.attrs)
        ///3 ÁªÑ‰ª∂Ê∏≤ÊüìÁöÑËäÇÁÇπÂØπË±°ËøîÂõû
        if (comp.base && comp.componentWillUpdate) comp.componentWillUpdate()
        if (comp.base) {
            if (comp.componentDidUpdate) componentDidUpdate()
        } else if (comp.componentDidMount) {
            comp.componentDidMount()
        }
        return comp.base
    }
    //ËôöÊãüdom
    const { tag, attrs, childrens } = vnode
    const dom = document.createElement(tag)
    if (attrs) {
        //ÈÅçÂéÜ
        Object.keys(attrs).forEach(key => {
            const value = attrs[key]
            setAttribute(dom, key, value)
        })
    }
    //ÈÅçÂéÜÂ≠êËäÇÁÇπ
    childrens.forEach(child => {
        render(child, dom)
    })
    return dom
}
//ËÆæÁΩÆÂ±ûÊÄß
function setAttribute(dom, key, value) {
    //Â∞ÜclassNameËΩ¨Êç¢Êàêclass
    if (key === 'className') {
        key = 'class'
    }
    //Â¶ÇÊûúÊòØ‰∫ã‰ª∂ onClick onBlur...
    if (/on\w+/.test(key)) {
        key = key.toLowerCase()
        dom[key] = value || ''
    } else if (key === 'style') {
        if (!value || typeof value === 'string') {
            dom.style.cssText = value
        } else if (value && typeof value === 'object') {
            //{width:20}
            for (let k in value) {
                if (typeof value[k] === 'number') {
                    dom.style[key] = value[k] + 'px'
                } else {
                    dom.style[key] = value[k]
                }
            }
        }
    }
    else {
        //ÂÖ∂‰ªñÂ±ûÊÄß
        if (key in dom) {
            dom[key] = value || ''
        }
        if (value) {
            dom.setAttribute(key, value)
        } else {
            dom.removeAttribute(key)
        }
    }
}
export default ReactDom